import React, {useEffect, useState} from 'react';
import './styles/characters/characters.css';

const Characters = () => {
    const [character, setCharacter] = useState([]);

    useEffect(() => {
        fetch('http://localhost:3000/characters')
        .then(response => response.json())
        .then(data => setCharacter(data));
    }, []);

    return (
        <section className='charactersContainer'>
            <h1>Personages</h1>
            <div>
                { character.map(myCharacter => {
                    const sexChararcter = myCharacter.sex;
                    const male = sexChararcter==="male" ? "he" : "she";
                    const good = myCharacter.Hero ? 'good' : 'bad';
                    return (<div>
                        <h1>{myCharacter.name}</h1>
                        <h2>{male} is {good}</h2>
                        <img src={myCharacter.image} alt={myCharacter.name} />
                    </div>)
                })}
            </div>
        </section>
    );
};

export default Characters;